import * as React from "react";
import InputLabel from "@mui/material/InputLabel";
import CssBaseline from "@mui/material/CssBaseline";
import Container from "@mui/material/Container";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";
import TextField from "@mui/material/TextField";

const Step2 = () => {
  const [selectedType, setType] = React.useState();
  const [selectedLift, setSelectedLift] = React.useState();
  const [lastService, setLastService] = React.useState("");
  const [isWorking, setIsWorking] = React.useState("");
  const [timeFrame, setTimeFrame] = React.useState("");

  const handleChangeService = (event) => {
    setType(event.target.value);
  };
  const handleChangeLift = (event) => {
    setSelectedLift(event.target.value);
  };
  const handleChange = (event) => {
    setLastService(event.target.value);
  };
  const handleTimeFrameChange = (event) => {
    setTimeFrame(event.target.value);
  };
  const handleIsWorkingChange = (event) => {
    setIsWorking(event.target.value);
  };

  return (
    <React.Fragment>
      <CssBaseline />
      <Container maxWidth="lg">
        <div style={{ margin: "30px 0" }}>
          <FormControl fullWidth>
            <InputLabel id="selected-service-type">
              Select Service Type
            </InputLabel>
            <Select
              labelId="selected-service-type"
              id="selected-service-type"
              value={selectedType}
              label="Select Service Type"
              onChange={handleChangeService}
            >
              <MenuItem value={1}>Annual Service</MenuItem>
              <MenuItem value={2}>General Service</MenuItem>
              <MenuItem value={3}>Call-out</MenuItem>
            </Select>
          </FormControl>

          <div style={{ marginTop: "30px" }}>
            <FormControl fullWidth>
              <InputLabel id="selected-lift">Select Lift</InputLabel>
              <Select
                labelId="selected-lift"
                id="selected-lift"
                value={selectedLift}
                label="Select Lift"
                onChange={handleChangeLift}
              >
                <MenuItem value={1}>Lift A</MenuItem>
                <MenuItem value={2}>Lift B</MenuItem>
                <MenuItem value={3}>Lift C</MenuItem>
              </Select>
            </FormControl>
          </div>

          {selectedLift && (
            <div style={{ textAlign: "left", padding: "30px 0px" }}>
              <div style={{ paddingBottom: "30px" }}>
                <p style={{ fontSize: "20px" }}>Your Service Requirements</p>

                <div style={{ paddingBottom: "15px" }}>
                  <FormControl fullWidth>
                    <InputLabel id="lastServiceTime">
                      When was the last professional elevator service?
                    </InputLabel>
                    <Select
                      labelId="lastServiceTime"
                      id="lastServiceTime"
                      value={lastService}
                      label="When was the last professional elevator service?"
                      onChange={handleChange}
                    >
                      <MenuItem value={"upTo12Months"}>
                        Up to 12 months ago
                      </MenuItem>
                      <MenuItem value={"1or2years"}>1 - 2 years</MenuItem>
                      <MenuItem value={"moreThan2Years"}>
                        More than 2 years
                      </MenuItem>
                      <MenuItem value={"never"}>Never</MenuItem>
                    </Select>
                  </FormControl>
                </div>

                <div style={{ paddingBottom: "15px" }}>
                  <FormControl fullWidth>
                    <InputLabel id="isWorking">
                      Is your elevator currently operational?
                    </InputLabel>
                    <Select
                      labelId="isWorking"
                      id="isWorking"
                      value={isWorking}
                      label="Is your elevator currently operational?"
                      onChange={handleIsWorkingChange}
                    >
                      <MenuItem value={"yes"}>Yes</MenuItem>
                      <MenuItem value={"no"}>No</MenuItem>
                    </Select>
                  </FormControl>
                </div>

                <div style={{ paddingBottom: "15px" }}>
                  <FormControl fullWidth>
                    <InputLabel id="timeFrame">
                      Preferred time frame:
                    </InputLabel>
                    <Select
                      labelId="timeFrame"
                      id="timeFrame"
                      value={timeFrame}
                      label="Preferred time frame:"
                      onChange={handleTimeFrameChange}
                    >
                      <MenuItem value={"urgently"}>Urgently</MenuItem>
                      <MenuItem value={"3or5Days"}>3 - 5 days</MenuItem>
                      <MenuItem value={"2weeks"}>Within 2 weeks</MenuItem>
                    </Select>
                  </FormControl>
                </div>

                <div style={{ paddingBottom: "15px" }}>
                  <TextField
                    id="notes"
                    label="Tell us if anything in particular requires attention:"
                    multiline
                    rows={4}
                    defaultValue=""
                    fullWidth
                  />
                </div>
              </div>
            </div>
          )}
        </div>
      </Container>
    </React.Fragment>
  );
};

export default Step2;
